@import url(https://fonts.googleapis.com/css?family=Cinzel+Decorative&display=swap);
body {
  font-family: "Cinzel Decorative", cursive;
  margin: 0;
  background-color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.no-marge {
  margin: 0px !important;
}
.no-pad {
  pad: 0px !important;
}
.title-center {
  padding-top: 50px;
}
.h1-size {
  font-size: 50px;
}
.h3-size {
  font-size: 25px;
}
.header-cont {
  font-family: "Cinzel Decorative", cursive;
  color: white;
  text-shadow: 2px 2px 20px white;
  background-color: rgb(24, 20, 92);
  background-image: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(17, 1, 158, 0.80)),
      to(rgba(67, 16, 150, 0.90))
    ),
    url(/click-game/static/media/bgtile.8eec607a.png);
  background-image: linear-gradient(
      to bottom,
      rgba(17, 1, 158, 0.80),
      rgba(67, 16, 150, 0.90)
    ),
    url(/click-game/static/media/bgtile.8eec607a.png);
    box-shadow: 0px -5px 100px inset rgb(117, 117, 0), 0px 5px 100px inset rgb(117, 117, 0) ;
  /* max-height: 20vh; */
}
.header-divs {
  position: relative;
  display: inline-block;
  width: 30%;
  margin: 0px;
}

.instruction {
  background-color: rgb(24, 20, 92);
  background-image: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(67, 16, 150, 0.90)),
      to(rgba(30, 0, 78, 0.999))),
    url(/click-game/static/media/bgtile.8eec607a.png);
  background-image: linear-gradient(
      to bottom,
      rgba(67, 16, 150, 0.90),
      rgba(30, 0, 78, 0.999)),
    url(/click-game/static/media/bgtile.8eec607a.png);
  font-family: "Cinzel Decorative", cursive;
  color: white;
  text-shadow: 2px 2px 20px white;
  padding: 0px 0px 20px 0px;
  margin: 0px !important;
  min-height: 20vh;
  position: relative;
  box-shadow: 0px 5px 25px inset rgb(117, 117, 0)
}
.card-table {
  font-family: "Cinzel Decorative", cursive;
  box-shadow: 0px -5px 10px rgb(117, 117, 0);
  background-image: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(117, 3, 3, 0.85)),
      to(rgba(119, 0, 0, 0.85))
    ),
    url(/click-game/static/media/bgrug.bc65d477.jpg);
  background-image: linear-gradient(
      to bottom,
      rgba(117, 3, 3, 0.85),
      rgba(119, 0, 0, 0.85)
    ),
    url(/click-game/static/media/bgrug.bc65d477.jpg);
  color: white;
  min-height: 90vh;
  box-shadow: 0px -5px 70px inset rgb(0, 5, 78), 0px 5px 70px inset rgb(8, 2, 98) ;
}

.card-img {
  max-height: 3in;
  max-width: 5in;
  padding: .5in;
}
.single-card {
  background-color: rgba(245, 245, 245, 0.4);
  font-family: "Cinzel Decorative", cursive;
  color: gold;
  text-shadow: 2px 2px 20px gold;
  border: 8px double white;
  margin: 8px;
  display: inline-block;
  max-height: 7in;
  max-width: 9in;
}
.single-card:hover {
  box-shadow: 0 5px 10px 0 gold,0 8px 30px 0 gold;
  text-shadow: 5px 5px 5px gold;
}

.button{
  font-size: 1.5em;
  background: brown;
  border: 5px double gold;
  border-radius: 8px;
  font-family: "Cinzel Decorative", cursive;
  color: gold;
  text-shadow: 2px 2px 4px gold;
  box-shadow: 0 12px 20px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
}

.button:hover {
  box-shadow: 0 12px 20px 0 gold,0 17px 60px 0 gold;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

